export function getUrlParams(url: string) {
    let obj: any = {},
        index = url.indexOf('?'), // url
        params = url.substr(index + 1); // url id = 1 & type = 2

    if (index !== -1) { // 
        let parr = params.split('&');  //  ["id = 1 ", " type = 2"]
        for (let i of parr) {           // 
            let arr = i.split('=');  // 1 i id = 1   arr = [id, 1]  2i type = 2  arr = [type, 2]
            obj[arr[0]] = arr[1];  // obj[arr[0]] = id, obj.id = 1   obj[arr[0]] = type, obj.type = 2
        }
    }
    return obj
}